import React from 'react'
import { Proyect } from './Proyect'
import "../assets/styles/proyects.css"
import inDevelop from '../assets/img/proyects/en-desarrollo.jpg';
import luceroNievas from "../assets/img/proyects/luceroNievas.JPG"
import glConcesionariaPreview from '../assets/img/proyects/gl-concesionaria-preview.PNG';
import minFinancePreview from '../assets/img/proyects/minFinance-preview.jpg';

export const Proyects = () => {
  return (
    <>
    <section className='proyects'>
      <h3 id='proyects' className='title'><span>.</span>Proyectos <span>() {"{"}</span></h3>
      <div className='proyects__container'>
        <Proyect 
        title="Landing Page Estudio Juridico (ficticio)" 
        imgUrl={luceroNievas} 
        webUrl={"https://ivan-lucero.github.io/LuceroNievas-Estudio-Juridico-LandingPage"} 
        githubUrl={"https://github.com/ivan-lucero/LuceroNievas-Estudio-Juridico-LandingPage"}
        />

        <Proyect 
        title="GL-Concesionaria" 
        imgUrl={glConcesionariaPreview}
        webUrl={"https://ivan-lucero.github.io/GL-concesionaria"} 
        githubUrl={"https://github.com/ivan-lucero/GL-concesionaria"}
        />

        <Proyect 
        title="minFinance - App de finanzas personales" 
        imgUrl={minFinancePreview} 
        githubUrl={"https://github.com/ivan-lucero/minfinance-back"}
        />
      </div>
      <span className='title'>{"}"}</span>
    </section>
    </>
  )
}
 